import { Component, OnInit, Output, Input } from '@angular/core';
import { Router, ActivatedRoute, NavigationEnd } from '@angular/router';
import { Subject } from 'rxjs';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.css']
})
export class HeaderComponent implements OnInit {
  @Input() menu: any;
  @Output() fornecedores: any;
  fornecedor: any = { codigo: 0, nome: "" };
  sbConfirmation = new Subject();
  menuRoute = "";
  menuAction = "";
  showFilter = false;
  showFornecedor = false;
  usuarioLogado = "";
  isMenuAction  = true;
  strAction = "";

  constructor(private route: Router) { }

  ngOnInit() {
    this.route.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        let result = this.route.config.filter(x => x.path == event.url.replace('/', ''))
        if (result.length > 0) {
          if (result[0].data !== undefined) {
            this.menuRoute = result[0].data.text.toUpperCase();
          }
        }
      }
    });
  }
  clickFornecedor(){}
  logout() {
   // this.openDialog("ConfirmaÃ§Ã£o de Logout", "Deseja sair do Sistema de GestÃ£o de Estoque?")
  }


}
